.work-app__card:nth-child(3) {
  width: 270px;
  height: 540px;
  grid-area: 1/2;
  margin: auto;
  grid-row-start: 1;
  grid-row-end: 3;
}
@media (max-width: 1070px) {
  .work-app__card:nth-child(3) {
    width: 210px;
    height: 420px;
  }
}
.work-app__btn {
  display: flex;
  justify-content: center;
}

.card__title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
}
.card__subtitle {
  line-height: 1.3;
}
.card__image {
  width: 100%;
  height: 100%;
  -o-object-fit: contain;
     object-fit: contain;
}

.message-btn {
  background: none;
  border: 2px solid #00b9b6;
  color: #00b9b6;
  padding: 14px 30px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
}

.num-list__item {
  width: 35px;
  height: 35px;
  background: rgba(0, 185, 182, 0.1);
  border-radius: 20px;
  color: #00b9b6;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.num-list__item:not(:last-child)::after {
  content: "";
  position: absolute;
  top: 45%;
  right: -80%;
  width: 20px;
  height: 4px;
  background: rgba(0, 185, 182, 0.1);
  border-radius: 2px;
}

.footer__wrap {
  display: flex;
  gap: 175px;
  flex-wrap: wrap;
  padding-top: 70px;
  padding-bottom: 70px;
}
@media (max-width: 1070px) {
  .footer__wrap {
    gap: 40px;
  }
}
.footer__logo {
  margin-bottom: 40px;
}
.footer__social {
  display: flex;
  gap: 20px;
  margin-bottom: 40px;
}
.footer__social svg {
  width: 40px;
  height: 40px;
}
.footer__social svg:nth-child(2) {
  background: linear-gradient(49.12deg, #ffdd55 6.62%, #ff543e 50.06%, #c837ab 93.5%);
  border-radius: 5px;
}
.footer__mail {
  margin-bottom: 20px;
  font-weight: 500;
}
.footer__mail svg {
  width: 24px;
  height: 24px;
}
.footer__adress {
  font-size: 18px;
  font-weight: 500;
}
.footer__adress svg {
  width: 24px;
  height: 24px;
}

.footer-menu__list {
  display: grid;
  grid-template-columns: repeat(3, minmax(100px, -webkit-max-content));
  grid-template-columns: repeat(3, minmax(100px, max-content));
  grid-template-rows: 60px 60px;
  align-items: center;
  gap: 0;
}
.footer-menu__link {
  font-weight: 500;
  color: #333333;
}